<template>
  <div class="mechanical">
    <div class="table-box">
      <el-input v-model="table.query.condition.condition"
                @keyup.enter.native="table.query.currentPage = 1;getData()"
                class="mr10" placeholder="//"></el-input>
      <div @click="table.query.currentPage = 1;getData()" class="btn-main ">&nbsp;&nbsp;&nbsp;</div>
<!--      <div @click="showDialog('add')" class="btn-main btn-add mb20 ml20"></div>-->
      <div @click="showDialog('add')" class="btn-main btn-add mb20"></div>
      <table cellspacing="0" class="table">
        <tr>
          <th width="6%"></th>
          <th width="13%"></th>
          <th class="left-justifying" width="10%"></th>
          <th width="10%"></th>
          <th width="10%"></th>
          <th width="13%"></th>
          <th width="10%"></th>
          <th width="15%"></th>
          <th width="13%"></th>
        </tr>
        <tr v-for="(item,key) in table.list">
          <td>{{key + 1}}</td>
          <td>{{item.plateNumber}}</td>
          <td class="left-justifying">{{item.machineryName}}</td>
          <td>{{item.machineryType | machineyrType}}</td>
          <td>{{item.nick}}</td>
          <td>{{item.mobile}}</td>
          <td>{{item.workRadius}}</td>
          <td>{{item.wristStrapCode}}</td>
          <td>
            <span @click="showDialog('edit',item)" class="oper"></span>
            <span class="col"></span>
            <span @click="remove(item)" class="oper"></span>
          </td>
        </tr>
        <no-data v-show="!table.list.length"></no-data>
      </table>
      <el-pagination
          class="pagination"
          :page-size="table.query.pageSize"
          @current-change="handleCurrentChange"
          :current-page.sync="table.query.currentPage"
          layout="prev, pager, next, jumper"
          :total="table.total">
      </el-pagination>
    </div>
    <el-dialog :modal="false" center :title="title"
               :close-on-click-modal="false"
               :visible.sync="dialog" width="420px">
      <el-form class="form" ref="form" label-width="85px" label-position="left">
        <el-form-item label="">
          <el-input v-model="addEditConfig.query.plateNumber" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="addEditConfig.query.machineryName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item prop="oldPassword" label="">
          <el-select v-model="addEditConfig.query.machineryType" placeholder="">
            <el-option label="" value="SMALL"></el-option>
            <el-option label="" value="MEDIUM"></el-option>
            <el-option label="" value="LARGE"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <div @click="showDriverDialog" class="btn-set">
            {{addEditConfig.query.nick || ''}}
          </div>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="addEditConfig.query.mobile" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input class="unit-input" type="number" v-model="addEditConfig.query.workRadius" placeholder=""></el-input>
          <span>cm</span>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="addEditConfig.query.wristStrapCode" placeholder=""></el-input>
        </el-form-item>

      </el-form>
      <div class="btns mt25">
        <div @click="editFun()" class="btn-main mr15"></div>
        <div @click="dialog = false" class="btn-main"></div>
      </div>
    </el-dialog>
    <!---->
    <choice-driver v-if="driverDialog" @on-submit="selectedDriver" v-model="driverDialog"></choice-driver>
  </div>
</template>

<script>
  import choiceDriver from './ChoiceDriver'

  export default {
    components: {
      choiceDriver
    },
    data() {
      return {
        table: {
          url: '/machinery/list',
          loading: '.table',
          list: [],
          total: 0,
          query: {
            condition: {
              condition: ''
            },
            currentPage: 1,
            pageSize: this.$conf.PAGES,
          }
        },
        driverDialog: false,
        // 
        addEditConfig: {
          url: '/mileage/add',
          query: {
            plateNumber: '',
            machineryName: '',
            machineryType: '',
            nick: '',
            mobile: '',
            workRadius: '',
            wristStrapCode: ''
          }
        },
        visible: false,
        title: '',
        dialog: false,
      }
    },
    methods: {
      // 
      showDialog(type, item) {
        this.dialog = true
        if (item) {
          this.title = ''
          this.addEditConfig.query = {
            id: item.id,
            userId: item.userId,
            plateNumber: item.plateNumber,
            machineryName: item.machineryName,
            machineryType: item.machineryType,
            nick: item.nick,
            mobile: item.mobile,
            workRadius: item.workRadius,
            wristStrapCode: item.wristStrapCode
          }
        } else {
          this.title = ''
          this.addEditConfig.query = {
            userId: '',
            plateNumber: '',
            machineryName: '',
            machineryType: '',
            nick: '',
            mobile: '',
            workRadius: '',
            wristStrapCode: ''
          }
        }
      },
      // 
      showDriverDialog() {
        this.driverDialog = true
      },
      // 
      selectedDriver(userID, userName){
        this.addEditConfig.query.userId = userID
        this.addEditConfig.query.nick = userName
      },
      // 
      async getData() {
        let res = await this.$ajax(this.table)
        this.table.list = res.pageItems || []
        this.table.total = res.totalCount
      },
      // 
      async remove(item) {
        await this.$confirm(``, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        })
        await this.$ajax({
          url: '/machinery/delete',
          query: {
            id: item.id
          }
        })
        this.$remind('')
        this.getData()
      },
      // 
      async editFun() {
        if(isNaN(this.addEditConfig.query.workRadius)){
          this.$message.error('')
          return
        }
        let text = ''
        if (this.title === '') {
          text = ''
          this.addEditConfig.url = '/machinery/update'
        } else {
          text = ''
          this.addEditConfig.url = '/machinery/add'
        }
        await this.$ajax(this.addEditConfig)
        this.$remind(text)
        this.dialog = false
        this.table.query = {
          condition: {
            condition: ''
          },
          currentPage: 1,
          pageSize: this.$conf.PAGES,
        }
        this.getData()
      },
      // 
      handleCurrentChange(e) {
        this.table.query.currentPage = e
        this.getData()
      },

    },
    mounted() {
      this.getData()
    },
    filters: {
      'machineyrType': function (val) {
        switch (val) {
          case 'SMALL':
            return ''
          case 'MEDIUM':
            return ''
          case 'LARGE':
            return ''
          default:
            return ''
        }
      }
    }
  }
</script>

<style lang="sass" scoped>
  .mechanical
    .table-box
      width: 100%

      .coordinates
        span
          display: inline-block
          width: 20px

        .el-input
          display: inline-block
          width: calc(100% - 20px) !important

      .assist
        font-size: $font-small
        color: $gray
        position: absolute
        top: 20px
        left: -80px

    .unit-input
      width: calc(100% - 25px) !important
      margin-right: 5px
    .btn-set
      width: 100%
      text-align: center
      height: 28px
      line-height: 28px
      border-radius: 5px
      color: $main-c
      border: 1px solid $main-c
      cursor: pointer
      display: inline-block
      &:hover
        opacity: .7
      &:active
        opacity: 1
</style>
